<template>
  <div>
    <div class="searchs">
      <el-form :inline="true" :model="searchs" class="form-search">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="账号">
              <el-input v-model="searchs.keyword_search_value" placeholder="请输入账号"></el-input>
            </el-form-item>
            <el-form-item label="状态">
              <el-select v-model="searchs.status" clearable placeholder="请选择">
                <el-option v-for="(value, name) in config.status" :key="name" :label="value" :value="name"> </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="订单号" align="center" />
      <el-table-column prop="cat_id_name" label="产品属性" align="center" />
      <el-table-column prop="goods_id_title" label="兑换产品" align="center" />
      <el-table-column prop="credit" label="积分" align="center" />
      <el-table-column prop="user_name" label="申请人姓名" align="center" />
      <el-table-column prop="user_mobile" label="申请人手机" align="center" />
      <el-table-column prop="name" label="收货人姓名" align="center" />
      <el-table-column prop="mobile" label="收货人手机" align="center" />
      <el-table-column prop="address" label="收货人地址" align="center" />
      <el-table-column prop="status_name" label="状态" align="center" />
      <el-table-column prop="uptime_date" label="添加时间" align="center" width="130" />
      <el-table-column prop="address" align="center" label="操作">
        <template slot-scope="scope">
          <el-button
            @click="show_dialog(scope.row.id, scope.row.status, scope.row.kd_sn)"
            v-if="scope.row.status === 'S'"
            type="success"
            size="small"
            >去发货</el-button
          >
          <el-button
            @click="show_dialog(scope.row.id, scope.row.status, scope.row.kd_sn)"
            v-if="scope.row.status === 'D'"
            type="primary"
            size="small"
            >查看/修改物流</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
    <el-dialog
      title="填写物流信息"
      :visible.sync="dialogFormVisible"
      :width="show_wl.show ? '80%' : '50%'"
      v-loading="loading"
    >
      <el-form :model="form">
        <el-form-item label="物流单号" label-width="120px" prop="dh">
          <el-input v-model="form.kd_sn" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="快递公司" label-width="120px">
          <el-select v-model="kd_value" placeholder="请选择快递公司">
            <el-option
              v-for="v in config.courier_company"
              :key="v.courier_name"
              :label="v.courier_name"
              :value="v.courier_name"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="快递状态" label-width="120px" prop="dh">
          <el-tag>{{ state_name }}</el-tag>
        </el-form-item>
        <el-timeline :reverse="true" v-if="show_wl.show">
          <el-timeline-item v-for="(activity, index) in wl_step" :key="index" :timestamp="activity.time">
            {{ activity.context }}
          </el-timeline-item>
        </el-timeline>
        <p v-if="wl_step.length == 0" v-html="no_msg"></p>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <!-- <el-button @click="dialogFormVisible = false" type="primary">确 定</el-button> -->
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="send">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import Fetch from '../../utils/fetch'

export default {
  name: 'aaaa',
  data() {
    return {
      loading: false,
      searchs: {
        keyword_search_value: '',
        status: '',
      },
      tableData: [],
      count: 0,
      page_curren: 1,
      config: {
        type: null,
        status: [1, 2, 3, 5],
      },
      id: 0,
      dialogFormVisible: false,
      form: {
        kd_sn: '',
        kd_name: '',
        kd_name_pinyin: '',
      },
      kd_value: '中通快递',
      kd_index: 0,
      no_msg: '没有找到物流信息哦~',
      show_wl: {
        show: false,
        kdgs: '',
        dh: '',
      },
      wl_step: [],
      state_name: '',
    }
  },
  mounted() {
    this.FetchList()
    this.getLogistics()
  },
  methods: {
    show_dialog(id, status, kd_sn) {
      this.id = id
      this.clear()
      if (status === 'D' && kd_sn) {
        this.show_wl.show = true
        Fetch('/goods/order/logisInfo', {
          id: id,
          kd_sn: kd_sn,
        })
          .then(r => {
            this.loading = false
            this.wl_step = r.data.kd_info
            this.kd_value = r.data.kd_name
            if (this.wl_step.length == 0) {
              this.no_msg = '没有找到物流信息哦~'
            }
            this.show_wl.kdgs = r.data.state_name
            this.form.kd_sn = r.data.kd_sn
            this.state_name = r.data.state_name || ''
          })
          .catch(() => (this.loading = false))
      } else {
        this.show_wl.show = false
        this.kd_value = '中通快递'
        this.no_msg = ''
      }

      this.dialogFormVisible = true
    },
    clear() {
      this.form.kd_sn = ''
      this.kd_index = 0
      this.kd_index = '中通快递'
    },
    send() {
      var that = this
      this.config.courier_company.forEach(function(v, k) {
        if (v.courier_name == that.kd_value) {
          that.kd_index = k
        }
      })

      this.form.kd_name = this.config.courier_company[this.kd_index].courier_name
      this.form.kd_name_pinyin = this.config.courier_company[this.kd_index].courier_name_pinyin
      if (this.form.kd_name == '' || this.form.kd_sn == '') {
        this.$message({
          message: '信息填写不完整',
          type: 'warning',
        })
        return
      }
      Fetch('/goods/order/update', {
        id: this.id,
        status: 'D',
        ...this.form,
      })
        .then(() => {
          this.loading = false
          this.dialogFormVisible = false
          this.FetchList()
          this.clear()
        })
        .catch(() => (this.loading = false))
    },
    emit(id) {
      this.$router.push('/notice/edit/' + id)
    },
    look(id) {
      this.$router.push('/notice/edit/' + id)
    },

    FetchList() {
      this.loading = true
      Fetch('/goods/order/search', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.config = r.data.config
        })
        .catch(() => (this.loading = false))
    },
    getLogistics() {
      this.loading = true
      Fetch('/goods/order/search', {
        page_curren: this.page_curren,
      })
        .then(r => {
          this.loading = false
          this.config = r.data.config
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
  },
}
</script>
